@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body {
  overflow-x: hidden;
  font-family: levijatan;
}
.loading-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.7);
}
.wrapper {
  max-width: 1200px;
  margin: 0 auto;
}
a {
  text-decoration: none;
}
@font-face {
  font-family: levijatan;
  src: url(/static/media/levijatan.3d515a15.TTF);
}
@font-face {
  font-family: ubuntu;
  src: url(/static/media/ubuntu.2505bfbd.TTF);
}

nav {
    display: flex;
    justify-content: center;
    color: #ffffff;;
    align-items: center;
}
nav ul {
    display: flex;
    padding: 0;
    margin: 0;
}
nav ul li {
    list-style: none;
    margin: 0 15px;
    cursor: pointer;
}
nav ul li a {
    text-decoration: none;
    transition: all 0.3s ease-in-out;
}
nav ul li a:hover {
    color: #444c44;;
}
.nav-logo {
    max-width: 80px;
    padding: 0 25px;
}
.nav-logo img {
    width: 100%;
}
.box-shadow {
    position: relative;
    box-shadow: 0px -3px 26px -12px rgba(255, 255, 255, 1);
    background-color: #000000;
    z-index: 9;
    transition: all 0.5s ease-in-out;
}
.sticky {
    position: fixed;
    top: 0;
    width: 100%;
    opacity: 1!important;
    visibility: visible!important;
    -webkit-animation-name: animation;
            animation-name: animation;
    -webkit-animation-duration: 0.6s;
            animation-duration: 0.6s;
}
.padding-body {
    padding-top: 82px;
}
.desktop-nav {
    margin-left: 30px;
    display: flex;
    align-items: center;
}
.nav-logo-mobile {
    max-width: 80px;
    padding-left: 10px;
}
.nav-logo-mobile img {
    width: 100%;
}
.mobile-nav {
    display: none;
    align-items: center;
    width: 100%;
    justify-content: space-between;
    padding-right: 10px;
}
.burger {
    display: inline-block;
    position: relative;
    cursor: pointer;
    z-index: 10;
}
.bar1, .bar2, .bar3 {
    width: 35px;
    height: 5px;
    background-color: #ffffff;
    margin: 6px 0;
    transition: 0.4s;
}

.change .bar1 {
    transform: rotate(-45deg) translate(-9px, 6px);
}

.change .bar2 {opacity: 0;}

.change .bar3 {
    transform: rotate(45deg) translate(-8px, -8px);
}
.nav-mobile-list {
    position: fixed;
    top: 0;
    width: 100%;
    left: 0;
    height: 100%;
    display: flex;
    align-items: center;
    background: rgba(0,0,0,0.8);
    z-index: 9;
    transform: translateX(100%);
    opacity: 0;
    transition: all 1s ease-in-out;
}
.show-mobile-nav {
    transform: translateX(0);
    opacity: 1;
}
.nav-mobile-list ul {
    flex-direction: column;
    text-align: center;
    width: 100%;
}
.nav-mobile-list ul li {
    margin: 10px 0;
}
.credit-cards {
    display: flex;
    font-family: "ubuntu"
}
.credit-cards .icon-wrapper {
    display: flex;
    align-items: center;
    margin: 10px;
}
.icon-wrapper i {
    margin-right: 5px;

}
@media only screen and (max-width: 893px) {
    nav ul li{
        margin: 0 8px!important;
    }
}
@media only screen and (max-width: 768px) {
    .desktop-nav {
        display: none
    }
    .mobile-nav {
        display: flex!important;
    }
    .footer-text {
        padding: 0 20px;
    }
    .credit-cards {
        display: flex;
        flex-direction: column;
    }
}

@-webkit-keyframes animation {
    from {
        opacity: 0;
        visibility: hidden;
    }
    to {
        opacity: 1;
        visibility: visible;
    }
}

@keyframes animation {
    from {
        opacity: 0;
        visibility: hidden;
    }
    to {
        opacity: 1;
        visibility: visible;
    }
}
.button-wrapper {
    min-width: 200px;
    margin: 30px;
    display: flex;
    justify-content: center;
}
.button-wrapper button {
    transition: all 0.3s ease-in-out;
    font-size: 20px;
    font-weight: 600;
    font-family: levijatan;
    letter-spacing: 3px;
    max-width: 200px;
}
.button-wrapper button:hover {
    background: rgba(255,255,255, 0.7);
}
.modal-wrapper {
    text-align: center;
    padding: 0 20px;
    color: #000;
}
.modal-wrapper p {
    font-family: ubuntu;
}
.modal-wrapper .button-wrapper button {
    color: #ffffff;
}
.modal-wrapper .button-wrapper button:hover {
    background: rgba(0, 0, 0, 0.9);
}
.modal-wrapper .credit-cards {
    flex-direction: column;
    align-items: center;
}
footer {
    color: #fff;
}
.footer-top {
    background-color: #0a0a0a;
}
footer .wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    align-items: center;
}
.footer-logo {
    max-width: 115px;
    position: relative;
}
.footer-logo img {
    width: 100%;
}
.footer-text p {
    text-align: justify;
    font-family: ubuntu;
}
.footer-text p span {
    color: red;
}

.footer-logo::before {
    display: inline-block;
    content: "";
    border-top: .2rem solid #fff;
    width: 10rem;
    margin: 0 1rem;
    transform: translateY(-1rem);
    position: absolute;
    left: -200px;
    top: 50%;
}
.footer-logo::after {
    display: inline-block;
    content: "";
    border-top: .2rem solid #fff;
    width: 10rem;
    margin: 0 1rem;
    transform: translateY(-1rem);
    position: absolute;
    right: -200px;
    top: 50%;
}
.footer-bottom {
    background-color: #000000;
}
.footer-bottom-box-img {
    max-width: 110px;
}
.footer-bottom-box-img img {
    width: 100%;
}
.footer-wrapper {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-between;
}
.footer-bottom-box {
    min-width: 200px;
}
.footer-wrapper li {
    color: #444c44;
    list-style: none;
    cursor: pointer;
}
.footer-wrapper li a {
    transition: all 0.3s ease-in-out;
}
.footer-wrapper li a:hover {
    color: #ffffff;
}
.footer-box-title {
    color: #ffffff!important;
}
.footer-icons a {
    font-size: 30px;
    color: #fff;
    margin: 10px;
    text-decoration: none;
    transition: all 0.3s ease-in-out;
}
.footer-icons a:hover {
    color: #444c44;;
}
.footer-wrapper ul {
    padding: 0;
}
.copyright {
    width: 100%;
    text-align: left;
    color: #444c44;
    font-family: ubuntu;
}
.footer-wrapper li a {
    color: #444c44;
}
.desktop-nav a {
    color: #ffffff;
    font-size: 20px;
}
.mobile-nav a {
    color: #ffffff;
    text-transform: uppercase;
}
.footer-title {
    font-size: 25px;
}
.footer-text-desc {
    text-align: right!important;
}
@media only screen and (max-width: 550px) {
    .footer-wrapper {
        flex-direction: column;
    }
    .footer-bottom-box-img {
        margin: 0 auto;
    }
    .copyright p {
        text-align: center;
    }
}
